
'use client';

import Image from 'next/image';
import { useRouter } from 'next/navigation';
import type { Building, User, BuildingStatus } from '@/types';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { ShieldAlert, CalendarDays, Users, Ticket, ArrowRight, Eye } from 'lucide-react';
import { cn } from '@/lib/utils';
import { format } from 'date-fns';
import { useToast } from '@/hooks/use-toast';

interface BuildingCardProps {
  building: Building;
  currentUser: User | null;
  // Removed competition prop as homepage will show all individual buildings
}

const getStatusBadgeClass = (status?: BuildingStatus) => {
  if (!status) return 'bg-muted text-muted-foreground';
  switch (status) {
    case '有效':
      return 'bg-green-500/20 text-green-400 border-green-500/30';
    case '等待审核':
      return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
    case '评审中': // Assuming this status might exist for buildings too
      return 'bg-purple-500/20 text-purple-400 border-purple-500/30';
    case '违规':
    case '作弊':
    case '无效':
      return 'bg-red-500/20 text-red-400 border-red-500/30';
    default:
      return 'bg-muted text-muted-foreground';
  }
};


export default function BuildingCard({ building, currentUser }: BuildingCardProps) {
  const { toast } = useToast();
  const router = useRouter();

  const status = building.status;
  const isViewable = status === '有效';

  let formattedDate = '日期未知';
  if (building.submissionDate) {
    try {
      formattedDate = format(new Date(building.submissionDate), 'yyyy年MM月dd日');
    } catch (e) { console.error("Date formatting error for building submission date", e); }
  } else if (building.eventYear) { // Fallback to event year if submissionDate is missing
     formattedDate = `${building.eventYear}年活动`;
  }
  
  const imageUrl = building.imageUrl || 'https://placehold.co/600x338.png';
  const aiHint = building.aiHint || "minecraft architecture";

  const handleCardClick = () => {
    if (isViewable) {
      router.push(`/building/${building.id}`);
    } else {
      toast({
        title: '访问受限',
        description: `此作品（状态：${status}）当前不可访问，请稍后再试。`,
        variant: 'default', // Consider a 'warning' variant if available/added
      });
    }
  };

  return (
    <Card 
      onClick={handleCardClick}
      className={cn(
        "bg-card border-border overflow-hidden shadow-lg hover:shadow-primary/20 transition-all duration-300 flex flex-col h-full group relative",
        isViewable ? 'cursor-pointer' : 'cursor-not-allowed opacity-80'
      )}
      tabIndex={0} // Make card focusable
      onKeyDown={(e) => { if (e.key === 'Enter' || e.key === ' ') handleCardClick(); }}
      role="button"
      aria-label={`查看作品 ${building.name}`}
    >
      <div className="relative aspect-[16/9] w-full overflow-hidden">
        <Image
          src={imageUrl.replace('800x600', '600x338').replace('600x400', '600x338')}
          alt={building.name}
          fill // Use fill instead of layout
          sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 33vw" // Example sizes
          className={cn(
            "object-cover transform transition-transform duration-300", // object-cover is important with fill
            isViewable && "group-hover:scale-105"
          )}
          data-ai-hint={aiHint}
        />
        <Badge className={cn(
            "absolute top-3 right-3 px-2 py-1 text-xs font-semibold rounded-md shadow-md", // Added shadow & rounded-md
            getStatusBadgeClass(status)
          )}>
          {status}
        </Badge>
        {!isViewable && (
          <div className="absolute inset-0 bg-destructive/30 backdrop-blur-sm flex flex-col items-center justify-center text-center p-4 z-10">
            <ShieldAlert className="h-10 w-10 text-destructive-foreground mb-2" />
            <p className="text-md font-semibold text-destructive-foreground">状态: {status}</p>
            <p className="text-xs text-destructive-foreground/80">此作品当前不可交互。</p>
          </div>
        )}
      </div>
      <CardHeader className="pt-4 pb-2 flex-shrink-0">
        <CardTitle className={cn(
          "text-lg font-semibold text-card-foreground leading-tight truncate", // Use truncate for long titles
          isViewable && "group-hover:text-primary transition-colors"
        )}>
          {building.name}
        </CardTitle>
      </CardHeader>
      <CardContent className="flex-grow pt-1 pb-3 space-y-2 text-sm">
        <div className="flex items-center text-muted-foreground gap-1.5">
          <CalendarDays size={14} /> 
          <span>{formattedDate}</span>
        </div>
        <p className="text-muted-foreground leading-relaxed line-clamp-2">{building.description}</p>
      </CardContent>
      <CardFooter className="flex justify-between items-center mt-auto pt-3 pb-4 px-4 border-t border-border/50 flex-shrink-0">
        <div className="flex items-center gap-3 text-xs text-muted-foreground">
          <div className="flex items-center gap-1">
            <Users size={14} />
            <span>{building.votes} 票</span>
          </div>
          <div className="flex items-center gap-1">
            <Ticket size={14} />
            <span>ID: {building.id.substring(0,6).toUpperCase()}</span>
          </div>
        </div>
        <Button 
          size="sm" 
          variant="ghost" 
          className={cn(
            "px-2 py-1 h-auto text-sm",
            isViewable ? "text-primary hover:bg-primary/10 hover:text-primary" : "text-muted-foreground cursor-not-allowed"
          )}
          disabled={!isViewable}
          aria-hidden="true" // Since the whole card is clickable, this button is more decorative
          tabIndex={-1} // Remove from tab order if card is main interactive element
        >
          {isViewable ? '查看作品' : '无法查看'}
          {isViewable && <ArrowRight size={16} className="ml-1" />}
        </Button>
      </CardFooter>
    </Card>
  );
}
