
'use client'; 

import { useEffect, useState } from 'react';
import type { User, Building } from '@/types';
import { getRawBuildings, fetchUserSessionForClient } from '@/app/actions';
import BuildingCard from '@/components/minecraft/BuildingCard';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { ServerCrash, Inbox } from 'lucide-react'; 
import { Skeleton } from '@/components/ui/skeleton';

export default function HomePage() {
  const [allWorks, setAllWorks] = useState<Building[]>([]);
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      try {
        setLoading(true);
        setError(null);
        const [worksData, userData] = await Promise.all([
          getRawBuildings().catch(e => { console.error("Failed to fetch all works", e); return []; }),
          fetchUserSessionForClient().catch(e => { console.error("Failed to fetch user session", e); return null; })
        ]);
        setAllWorks(worksData);
        setCurrentUser(userData);
      } catch (err) {
        console.error("Error fetching homepage data:", err);
        setError("无法加载作品数据，请稍后再试。");
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="space-y-6">
        <div className="h-8 bg-muted rounded w-1/3 animate-pulse mb-4"></div> {/* Title Skeleton */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 sm:gap-6">
          {[...Array(8)].map((_, i) => ( // Show more skeletons for all works
            <div key={i} className="bg-card border-border rounded-lg shadow-lg flex flex-col overflow-hidden">
              <div className="w-full aspect-[16/9] bg-muted animate-pulse"></div>
              <div className="p-4 flex flex-col flex-grow">
                <Skeleton className="h-5 bg-muted rounded w-3/4 mb-2" />
                <Skeleton className="h-3 bg-muted rounded w-1/2 mb-1" />
                <Skeleton className="h-3 bg-muted rounded w-full mb-3" />
                <Skeleton className="h-3 bg-muted rounded w-2/3 mb-4" />
                <div className="mt-auto flex justify-between items-center">
                    <div className="flex gap-3">
                        <Skeleton className="h-4 bg-muted rounded w-12" />
                        <Skeleton className="h-4 bg-muted rounded w-10" />
                    </div>
                    <Skeleton className="h-8 bg-muted rounded w-24" />
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <Alert variant="destructive" className="mt-6">
        <ServerCrash className="h-5 w-5" />
        <AlertTitle>数据加载错误</AlertTitle>
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  return (
    <div className="space-y-6">
      <h2 className="text-3xl font-bold text-foreground tracking-tight">
        作品画廊
      </h2>
      
      {allWorks.length > 0 ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 sm:gap-6">
          {allWorks.map((building) => (
            <BuildingCard 
              key={building.id} 
              building={building} 
              currentUser={currentUser} 
            />
          ))}
        </div>
      ) : (
         <Alert className="border-primary/30 bg-primary/5 text-center py-10">
            <Inbox className="h-12 w-12 text-primary mx-auto mb-4" />
            <AlertTitle className="text-xl font-semibold text-primary/90 mb-2">暂无作品</AlertTitle>
            <AlertDescription className="text-primary/80">
                目前还没有任何作品提交。敬请期待！
            </AlertDescription>
        </Alert>
      )}
    </div>
  );
}
